.MuiModal-root {
  background-color: transparent !important;
}

.close-modal {
  border: solid 1px #ccc;
  border-radius: 50%;
  width: 28px;
  height: 28px;
  background-color: #fff;
  float: right;
  margin-top: 2px;
  margin-right: 5px;
  cursor: pointer;
}
.MuiTooltip-tooltip{
  font-size: 14px !important;
}

.MuiDataGrid-columnHeaders, .MuiDataGrid-cellContent {
  /* font-size: clamp(13px, 1vw, 1rem) !important; */
  color: #000;
  font-family: Arial !important;
}

.f11 .MuiDataGrid-columnHeaders, .f11 .MuiDataGrid-cellContent, .f11 .MuiDataGrid-cell {
  font-size: 11px !important;
}

.f11 .panel-title {
  font-size: 13px !important;
}

@media(max-width: 520px) {
  .MuiDataGrid-panelContent {
    max-width: 320px;
  }
}