/* Creating the css styles to TabPanel's table */
.row-class {
    display: flex;
    justify-content: left;
    align-items: flex-start;
    border-bottom: solid 0.5px rgba(0, 0, 0, 0.055);
    border-top: solid 0.5px rgba(0, 0, 0, 0.077);
    overflow: hidden;
    padding-left: 10px;
    padding-right: 5px;
    font-size: 0;
}

.row-class:hover {
    background-color: rgba(69, 69, 69, 0.1);
}

.column-class {
    text-align: justify;
    font-size: 0;
}

.column-class a {
    color: #333333;
    font-family: Arial !important;
    font-size: 0.9rem;
    margin: 0;
    padding: 0;
}

.bgnd-dark {
    background-color: rgba(69, 69, 69, 0.055);
}

.MuiTabs-root .MuiTab-root {
    min-width: auto;
    height: auto !important;
    min-height: auto !important;
    padding: 5px;
    font-size: 0.8rem;
    display: inline-flex;
    flex-wrap: nowrap;
    outline: none;
}

.MuiTabs-root .MuiTabs-indicator {
    display: none;
}

.MuiTabs-root .MuiTab-root::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #333333;
    transform: scaleX(0);
    transform-origin: bottom left;
    transition: transform 0.3s ease;
}

.combined-column {
    display: flex;
    align-items: center;
}

.icon-container {
    display: flex;
    align-items: center;
    margin-right: 5px;
    /* Espacio entre los iconos */
}

/* Ajusta el tamaño y el espacio de los iconos según sea necesario */
.icon-container svg {
    margin-right: 5px;
    /* Espacio entre los iconos PDF y DOC */
}

.name {
    flex: 1;
    /* Para ocupar el espacio restante y alinear el nombre a la izquierda */
    /* Si el texto es largo, mostrar puntos suspensivos */
    white-space: wrap;
    /* Evita que el texto se divida en varias líneas */
}

.MuiTabs-root .MuiTab-root:hover::after {
    transform: scaleX(1);
}

.smallTabs {
    height: auto !important;
    min-height: auto !important;
}

.MuiTabs-scrollButtons {
    width: 15px !important;
    height: 24px !important;
    opacity: 1 !important;
    pointer-events: auto !important;
}

.MuiTabs-scrollButtons .MuiIconButton-root {
    width: 15px !important;
    height: 24px !important;
    padding: 0 !important;
    margin: 0 !important;
}

.MuiTabs-root .MuiTab-root.Mui-selected {
    color: #333333;
    font-weight: bold;
    background-color: rgba(0, 0, 0, 0.070);
}